html, body {
    background-color: $white
}

a:visited {
    color: #6e3e98;
}


header {
    //background: rgba(132,205,242,1);
    //background: linear-gradient(345deg, rgba(255,239,121,1) 0%, rgba(178,237,255,1) 33%, rgba(132,205,242,1) 49%, rgba(92,176,245,1) 70%, rgba(42,147,252,1) 84%, rgba(25,77,238,1) 100%);
    background-color: #151A14;
    background-image: url(/assets/img/masthead_background_1x.jpg);
    background-size: 960px 340px;
    background-repeat: repeat-x;
    background-position: top center;
    margin-bottom: 2rem !important;
    @include breakpoint(small only) {
        background-position-y: 22%;
        margin-bottom: 1rem !important;
    }

    > .masthead {
        @include breakpoint(medium up) {
            height: 14.75rem;
        }
        padding-top: 0.8rem;
    }

    h1 {
        img {
            width: 100%;
            max-width: 360px;
        }
    }

    > nav {
        font-size: 1.2rem;
        background-color: rgba(0,0,0,0.4);
        padding: 0.6rem 0;

        ul.menu li {
            a {color: #8AA254;}
            &.is-active > a {
                background: none;
            }
        }
    }
}

@media
  (-webkit-min-device-pixel-ratio: 1.5),
  (   min--moz-device-pixel-ratio: 1.5),
  (        min-device-pixel-ratio: 1.5),
  (                min-resolution: 1.5dppx),
  (                min-resolution: 144dpi) {
    header {
        background-image: url(/assets/img/masthead_background_2x.jpg);
    }
}

.body_container {
    margin-bottom: 2rem;
}

main {
    height: 100%;
    font-size: 1.15rem;

    &.page {
        padding: 0 1.2rem;
    }

    &.article {
        article.post {
            padding: 0 1.2rem 0.8rem 1.2rem;
        }
        .separator {
            padding: 1.2rem 0;
        }
    }
}

article {
    font-size: 1.1rem;

    h2 {
        & > a, & > a:visited {
            color: $body-font-color;
        }
    }
    .byline {
        margin-bottom: 1em;
        color: $dark-gray;
    }
}

figure {
    @mixin figure_float($position) {
        @include breakpoint(medium up) {
            @if $position == left {
                padding-right: 1rem;
            } @else if $position == right {
                padding-left: 1rem;
            }
            width: 50%
        }
    }

    &.float-right {@include figure_float(right)}
    &.float-left  {@include figure_float(left)}
}

figcaption {
    font-size: 0.85em;
}

ul.no-bullet ul.no-bullet {
    margin-left: 1.25rem;
}

nav.sidebar {
    font-size: 1.1rem;

    h1 {
        font-size: 1.6rem;
        margin: 0;
    }
    img {
        height: 1em;
        vertical-align: top;
        margin-right: 0.4em;
    }

    .menu {
        margin-bottom: 1rem;

        a {color: $body-font-color;}
    }

    .tag-cloud {
        font-size: 1rem;

        a:hover {
            text-decoration: underline;
        }
    }
}

@import "share_links"



